$col-num: 7;
$padding: 10px;
$cell: 100px;
$gap: 1px;
$calendar-width: ($col-num * $cell) + (6 * $gap);
// stylelint-disable-next-line scss/dollar-variable-colon-space-after
$days:
  'mon' 1,
  'tue' 2,
  'wed' 3,
  'thu' 4,
  'fri' 5,
  'sat' 6,
  'sun' 7;
$marg-left: ($cell + $gap);
$font-family: Arial, sans-serif;
$f-s: 30px;
$num-color: #000;
